export default { 
	data() {
		return {
			picList: [],
			indicatorDots: false,
			autoplay: false,
			duration: 500,
			circular: true,
			current: 0,
			isShowSwiper: false,
			img:'',
			imgType:true,
		};
	},
	onLoad(option) {
		this.index(option);
	}, 
	methods: {
		index(option){
			var url=JSON.parse(decodeURIComponent(option.url)).toString();
			var notS = url.split(':')[0];
			var a = notS.indexOf('ps') > -1;
			if(a){
				this.picList.push(url);	
				this.imgType=true;
			}else{
				this.imgType=false;
				var num=Math.floor(Math.random()*8+1);
				this.img='../../static/images/icon/img/title'+num+'.png'
			}			
		},
		clickPic(index) {
			this.current = index;
			this.isShowSwiper = true;
		},
		changeSwiper(e) {
			this.current = e.target.current;
		}
	}
};
